@keyframes sliding {
  from {
    transform: translateX(0%); }
  to {
    transform: translate(-34%); } }

.slider .slider_mask {
  overflow: hidden;
  position: relative; }
  .slider .slider_mask::before, .slider .slider_mask::after {
    content: '';
    position: absolute;
    top: 0;
    width: 2rem;
    height: 100%;
    z-index: 1; }
  .slider .slider_mask::before {
    background-image: linear-gradient(to right, #fafafa, transparent 50%);
    left: 0; }
  .slider .slider_mask::after {
